<template>
  <div id="box" class="bg">
    <div class="conent">
      <div class="header-img">
        <img class="headerImg" src="~@/assets/images/headerTop.png" alt="">
      </div>
      <div class="contanier">
        <h4>{{Title}}</h4>
        <p class="SpanText border-bottom">{{Text}}</p>
        <form action="" @submit.prevent="submit">
          <div class="form-wrap">
            <label>
              <div class="form-wrap-label label" for="">{{DataForm.FormLabel}}<span class="labelColor">*</span></div>
              <input class="form-wrap-input" type="text"  @blur.prevent="name" ref="nameRef" placeholder="请输入您的姓名"  v-model="inputText.name"/>
              <span class="tipSpan" v-show="!nameShow">请输入正确的姓名</span>
            </label>
          </div>
          <div class="form-wrap">
             <label class="form-wrap-label" for="">
              {{DataForm.FormTpl}}<span class="labelColor">*</span>
             </label>
            <input class="form-wrap-input" type="text" @blur.prevent="Tpl" ref="tplRef" v-model="inputText.tpl" placeholder="请输入您的手机号"/>
            <span class="tipSpan" v-show="!tplShow">请输入11位手机号</span>
          </div>
           <div class="form-wrap">
            <label class="form-wrap-label" for="">
              {{DataForm.FormImg}}<span class="labelColor">*</span>
            </label>
            <div class="form-fexl">
              <input class="flex-left ImgLet" type="text" @blur.prevent="ewq" v-model="inputText.ImgVer" ref="imgRef"/>
              <div class="flex-right"><img src="~@/assets/images/user1.png" alt=""></div>
            </div>
            <span class="tipSpan" v-show="!ImgDataShow">不能为空</span>
            <div class="form-fexl aa">
              <input class="flex-left" type="text" placeholder="请输入验证码" @blur.prevent="tplForm" v-model="inputText.ver" ref="tplData">
              <button class="flex-right" @click="handelClick">
                <span v-if="sendMsgDisabled">{{time+'s'}}</span>
                <span v-if="!sendMsgDisabled">点击获取短信</span>
              </button>
            </div>
            <span class="tipSpan" v-show="!TplDataShow">不能为空</span>
          </div>
          <div class="form-wrap">
            <label class="form-wrap-label" for="">
              {{DataForm.FormText}}<span class="labelColor">*</span>
            </label>
            <input class="form-wrap-input" type="text" @blur.prevent="verificationText" v-model="inputText.Text" ref="textRef"/>
            <span class="tipSpan" v-show="!TextShow">不能为空</span>
          </div>
          <div class="form-wrap">
            <label class="form-wrap-label" for="">{{DataForm.FormManyText}}<span class="labelColor">*</span></label>
            <textarea name="" id="" class="form-wrap-textarea" cols="30" rows="10" @blur.prevent="verificationManyText" v-model="inputText.ManyText" ref="ManyRef"></textarea>
            <span class="tipSpan" v-show="!ManyShow">不能为空</span>
          </div>
          <div class="form-wrap bg-Many">
            <label class="form-wrap-label" for="">一<span class="labelColor">*</span></label>
            <div>
              <div>
                <label for="">男</label>
                <input type="radio" value="man" v-model="inputText.sex">
              </div>
              <div>
                <label for="">女</label>
                <input type="radio" value="woman" v-model="inputText.sex">
              </div>
            </div>
            <span class="tipSpan" v-show="!SexShow">请选择性别</span>
            <textarea name="" id="" class="form-wrap-textarea" cols="30" rows="10"></textarea>
          </div>
           <div class="form-wrap bg-Many">
            <label class="form-wrap-label" for="">二<span class="labelColor">*</span></label>
            <div>
              <label for="">A</label>
              <input type="checkbox" v-model="inputText.CheckboxA" value="A">
            </div>
            <div>
              <label for="">B</label>
              <input type="checkbox" v-model="inputText.CheckboxB"  value="B">
            </div>
            <textarea name="" id="" class="form-wrap-textarea" cols="30" rows="10"></textarea>
            <span class="tipSpan" v-show="!Checked">请选择性别</span>
          </div>
          <div class="form-wrap FormButton" >
            <button type="submit">提交</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>

export default{
  name: 'Form',
  data () {
    return {
      Title: '报名问卷名称',
      Text: '报名问卷描述报名问卷描述',
      DataForm: {
        FormLabel: '姓名',
        FormTpl: '手机号',
        FormImg: '图形验证码 ',
        FormText: '单行文字 ',
        FormManyText: '多行文字',
        FormSex: '性别'
      },
      option: [],
      nameShow: true,
      time: 60, // 发送验证码倒计时
      sendMsgDisabled: false,
      tplShow: true,
      ImgDataShow: true,
      TplDataShow: true,
      TextShow: true,
      ManyShow: true,
      SexShow: true,
      Checked: true,
      inputText: { }
    }
  },
  methods: {
    name () {
      const name = this.$refs.nameRef
      let val = name.value
      const mobile = /^[\u4E00-\u9FA5A-Za-z]{2,15}$/.test(val)
      if (!mobile) {
        name.style.border = '1px solid red'
        this.nameShow = false
        name.focus()
      } else {
        name.style.border = '1px solid #e5e5e5'
        this.nameShow = true
      }
    },
    Tpl () {
      const tpl = this.$refs.tplRef
      let val = tpl.value
      const mobile = /^1[3|4|5|7|8][0-9]{9}$/.test(val)
      if (!mobile) {
        tpl.style.border = '1px solid red'
        this.tplShow = false
        tpl.focus()
      } else {
        tpl.style.border = '1px solid #e5e5e5'
        this.tplShow = true
      }
    },
    handelClick () {
      let me = this
      me.sendMsgDisabled = true
      let interval = window.setInterval(function () {
        if ((me.time--) <= 0) {
          me.time = 60
          me.sendMsgDisabled = false
          window.clearInterval(interval)
        }
      }, 1000)
    },
    ewq () {
      const ImgVerification = this.$refs.imgRef
      let val = ImgVerification.value
      if (val.length < 1) {
        ImgVerification.style.border = '1px solid red'
        this.ImgDataShow = false
        ImgVerification.focus()
      } else {
        ImgVerification.style.border = '1px solid #e5e5e5'
        this.ImgDataShow = true
      }
    },
    tplForm () {
      const FormTpl = this.$refs.tplData
      let val = FormTpl.value
      if (val.length < 1) {
        FormTpl.style.border = '1px solid red'
        this.TplDataShow = false
        FormTpl.focus()
      } else {
        FormTpl.style.border = '1px solid #e5e5e5'
        this.TplDataShow = true
      }
    },
    verificationText () {
      const Text = this.$refs.textRef
      let val = Text.value
      if (val.length < 1) {
        Text.style.border = '1px solid red'
        this.TextShow = false
        Text.focus()
      } else {
        Text.style.border = '1px solid #e5e5e5'
        this.TextShow = true
      }
    },
    verificationManyText () {
      const ManyText = this.$refs.ManyRef
      let val = ManyText.value
      if (val.length < 1) {
        ManyText.style.border = '1px solid red'
        this.ManyShow = false
        ManyText.focus()
      } else {
        ManyText.style.border = '1px solid #e5e5e5'
        this.ManyShow = true
      }
    },
    submit () {
      let name = this.$refs.nameRef
      let tpl = this.$refs.tplRef
      let graphic = this.$refs.imgRef
      let sms = this.$refs.tplData
      let text = this.$refs.textRef
      let manytext = this.$refs.ManyRef
      let sex = this.inputText.sex
      let A = this.inputText.checkboxA
      let B = this.inputText.CheckboxB
      name.value.length <= 0 ? name.focus() : true
      tpl.value.length <= 0 ? tpl.focus() : true
      graphic.value.length <= 0 ? graphic.focus() : true
      sms.value.length <= 0 ? sms.focus() : true
      text.value.length <= 0 ? text.focus() : true
      manytext.value.length <= 0 ? manytext.focus() : true
      sex == null ? this.SexShow = false : this.SexShow = true
      A || B == null ? this.Checked = false : this.Checked = true
      console.log(this.inputText)
    }
  }
}
</script>
<style lang="stylus" scoped>
  .form-wrap >>> .tipSpan
    color red
    font-size 14px
    display block
    margin-top 5px
  @media screen and (max-width 780px) {
    #box .bg{
      width 12rem
    }
    #box .conent{
      width 100%
      margin 0
    }
    #box .header-img{
      height 2.5rem
    }
    #box .contanier{
      width 90%
      padding-left 5%
      padding-right 5%
    }
    #box .header-img{
      padding-top 0
    }
    #box .form-wrap.bg-Many{
      width 90%
    }
    #box .headerImg{
      border-radius 0
    }
  }
  .bg
    width 100%
    background #eee
    min-height 1200px
    .conent
      width 12rem
      margin 0 auto
      background #fff
      .header-img
        width 100%
        height 4.688rem
        padding-top 75px
        background #eee
        .headerImg
          width 100%
          height 100%
          border-radius 8px 8px 0 0
      .contanier
        padding-left .5rem
        padding-right .5rem
        h4
          margin .4rem 0
          font-size .68rem
        .SpanText
          font-size .36rem
          padding 0 0 .4rem 0
        .form-wrap
          margin-top .6rem
          width 100%
          .form-wrap-label
            display block
            font-size .3rem
            .labelColor
              color red
              padding-left .08rem
          .form-wrap-input
            height .6rem
            width 100%
            margin-top .2rem
            border .02rem solid #fafafa
            border-radius .1rem
            background #fafafa
            text-indent .2rem
          .form-wrap-textarea
            width 100%
            margin-top .2rem
            height 2rem
            min-height 1.2rem
            border .02rem solid #fafafa
            resize none
            background #fafafa
            border-radius .1rem
          .form-fexl.aa
            margin-top .6rem
          .form-fexl
            display flex
            height .6rem
            margin-top .2rem
            input
              height .6rem
              border .02rem solid #fafafa
              border-radius .1rem
              background #fafafa
              text-indent .2rem
            .flex-left.ImgLet
              margin-right 2rem
            .flex-left
              flex 1
            .flex-right
              width 2rem
              margin-left .5rem
            button
              border-radius .1rem
              background #fff
              color #4dace3
              border solid 1px #4dace3
            img
              width 100%
              height 100%
        .form-wrap.bg-Many
          width 93%
          padding .4rem
          border-radius .2rem
          background #f9f9f5
          div
            margin-top .2rem
          .form-wrap-textarea
            background #fff
        .FormButton
          width 100%
          text-align center
        .FormButton button
          margin .5rem 0
          padding .08rem .4rem
          color #fff
          border none
          border-radius .12rem
          outline 0 none
          font-size .3rem
          background #4dace3
</style>



